<template>
	<div class="OrderPage">
		<van-nav-bar fixed title="我的订单" left-text="返回" left-arrow @click-left="goBack" />
		<Mymessage>
			<div class="order-box">
				<van-tabs v-model="active" @change="changeOrderPage">
					<!-- 全部订单 -->
					<van-tab title="全部">
							<van-empty v-if="AllOrderData.length<=0" description="您暂时还没有订单哦!" />
						<div class="all-box" v-for="(item,index) in AllOrderData" :key="index">
							<div class="box-top">
								<div class="top-left">{{item.oid}}</div>
								<div class="right1 top-right" v-if="item.status==1" @click="changeState(item,index)">确认收货</div>

								<div class="right2 top-right" v-else @click="removeOrder(item,index)">已完成
									<van-icon name="delete-o" />
								</div>
							</div>
							<div class="box-content">
								<OrderItem :orderData="item.data"></OrderItem>
							</div>
							<div class="box-bottom">
								<div class="time">{{item.proInfo.data|formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
								<div class="bottom-item">
									<div class="bot-left">共计 {{item.proInfo.count}} 件</div>
									<div class="bot-right">合计 &yen; {{item.proInfo.total}}</div>
								</div>
							</div>
						</div>

					</van-tab>
					<!-- 进行中订单 -->
					<van-tab title="进行中">
						<van-empty v-if="ConductOrderData.length<=0" description="您暂时还没有进行中订单哦!" />
						<div class="conIng-box all-box" v-for="(item,index) in ConductOrderData" :key="index">
							
							<div class="box-top">
								<div class="top-left">{{item.oid}}</div>
								<div class="top-right" @click="changeState(item,index)">确认收货
								</div>
							</div>
							<div class="box-content">
								<OrderItem :orderData="item.data"></OrderItem>
							</div>
							<div class="box-bottom">
								<div class="time">{{item.proInfo.data|formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
								<div class="bottom-item">
									<div class="bot-left">共计 {{item.proInfo.count}} 件</div>
									<div class="bot-right">合计 &yen; {{item.proInfo.total}}</div>
								</div>
							</div>
						</div>
					</van-tab>
					<!-- 已完成订单 -->
					<van-tab title="已完成">
						<van-empty v-if="CompleteOrderData.length<=0" description="您暂时还没有已完成的订单哦!" />
						<div class="completed-box all-box" v-for="(item,index) in CompleteOrderData" :key="index">
							<div class="box-top">
								<div class="top-left">{{item.oid}}</div>
								<div class="top-right" @click="removeOrder(item,index)">已完成
									<van-icon name="delete-o" />
								</div>
							</div>
							<div class="box-content">
								<OrderItem :orderData="item.data"></OrderItem>
							</div>
							<div class="box-bottom">
								<div class="time">{{item.proInfo.data|formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
								<div class="bottom-item">
									<div class="bot-left">共计 {{item.proInfo.count}} 件</div>
									<div class="bot-right">合计 &yen; {{item.proInfo.total}}</div>
								</div>
							</div>
						</div>
					</van-tab>
				</van-tabs>
			</div>
		</Mymessage>

	</div>
</template>

<script>
	import Mymessage from "../components/Mymessage.vue"
	import "../assets/less/OrderPage.less"
	import OrderItem from "../components/OrderItem.vue"
	export default {
		name: "OrderPage",
		data() {
			return {
				active: 0,
				tokenString: "",
				//全部订单数据
				AllOrderData: [],
				//进行中订单数据
				ConductOrderData: [],
				//已完成订单数据
				CompleteOrderData: [],
				//保存前一个的路径
				oldPath:null
			}
		},
		components: {
			Mymessage,
			OrderItem
		},
		created() {
			this.getOrderData(0)
		},
		methods: {
			goBack() {
				if(this.oldPath=="/paycomplete"){
					this.$router.push({name:"Main"})
				}else{
					this.$router.go(-1)
				}
			},

			//获取所有订单数据
			getOrderData(status) {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});

				//请求方式
				this.axios({
					method: 'GET',
					url: '/findOrder',
					//如果是post请求,参数需要挂载到data
					params: {
						tokenString: this.tokenString,
						status: status
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					// console.log(res);
					if (res.data.code === 70000) {

						let products = [];
						//订单id数组
						let oids = []

						//找出不同的oid
						res.data.result.forEach(v => {
							v.small_img = v.smallImg;
							if (oids.indexOf(v.oid) === -1) {
								let obj = {
									oid: v.oid,
									status: v.status,
									proInfo: {
										data: v.updatedAt,
										count: 0,
										total: 0
									},
									data: []
								};
								oids.push(v.oid);
								products.push(obj)
							}
						})
						// console.log(oids);
						// console.log(products);

						//根据oid查找相应的订单商品
						products.forEach(value => {
							res.data.result.forEach(item => {
								if (value.oid === item.oid) {
									value.data.push(item)
									value.proInfo.count += item.count
									value.proInfo.total += item.count * item.price;
								}
							})
						})
						// console.log(oids);
						// console.log(products);
						//全部订单信息
						this.AllOrderData = products
						// console.log(this.AllOrderData);

						//每次切换前先清空之前的数据
						this.ConductOrderData = []
						this.CompleteOrderData = []
						// 筛选当前订单状态
						this.AllOrderData.forEach(item => {
							if (item.status === 1) {
								//进行中
								this.ConductOrderData.push(item)
							} else if (item.status === 2) {
								//已完成 
								this.CompleteOrderData.push(item)
							}
						})

					}

				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//改变订单当前的收货等状态
			changeState(item, index) {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});

				//请求方式
				this.axios({
					method: 'POST',
					url: '/receive',
					//如果是post请求,参数需要挂载到data
					data: {
						tokenString: this.tokenString,
						oid: item.oid
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					// console.log(res);
					if (res.data.code === 80000) {
						item.status = 2
						//如果是在进行中页面的时候确认收货
						if (this.active == 1) {
							this.ConductOrderData.splice(index, 1)
						}
					}
					this.$toast.loading({
						message: res.data.msg,
						forbidClick: true,
						dutation: 500
					});

				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//切换订单页面
			changeOrderPage(name) {
				this.getOrderData(name)
			},

			//删除订单
			removeOrder(item, index) {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});

				//请求方式
				this.axios({
					method: 'POST',
					url: '/removeOrder',
					//如果是post请求,参数需要挂载到data
					data: {
						tokenString: this.tokenString,
						oid: item.oid
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					if (res.data.code === 90000) {
						this.AllOrderData.splice(index, 1)
					}
					this.$toast.loading({
						message: res.data.msg,
						forbidClick: true,
						dutation: 500
					});

				}).catch(eer => {
					console.log("eer==>", eer);
				})
			}
			
		},
		//获取来的路径
		beforeRouteEnter(to, from, next) {
			next(vm => {
				vm.oldPath = from.path
				// console.log("vm.oldPath==>", vm.oldPath);
			});
		
		}

	}
</script>
